const valid = ({firstName, lastName, username, email, password, confirmPassword}) => {
    const err = {}

    if(!firstName) {
        err.firstName = "Please add your first name."
    }else if(firstName.length > 10){
        err.firstName = "Full name is up to 10 characters long."
    }

    if(!lastName) {
        err.lastName = "Please add your last name."
    }else if(lastName.length > 10){
        err.lastName = "Full name is up to 10 characters long."
    }

    if(!username) {
        err.username = "Please add your user name."
    }else if(username.replace(/ /g, '').length > 25){
        err.username = "User name is up to 25 characters long."
    }

    if(!email) {
        err.email = "Please add your email."
    }else if(!validateEmail(email)){
        err.email = "Email format is incorrect."
    }

    if(!password) {
        err.password = "Please add your password."
    }else if(password.length < 6){
        err.password = "Password must be at least 6 characters."
    }

    if(password !== confirmPassword) {
        err.confirmPassword = "Confirm password does not match."
    }

    return {
        errMsg: err,
        errLength: Object.keys(err).length
    }
}

const validEmail = ({email}) => {
    const err = {}
    if (!email) {
        err.email = "Please add your email."
    } else if(!validateEmail(email)){
        err.email = "Email format is incorrect."
    }
    return {
        errMsg: err,
        errLength: Object.keys(err).length
    }
}

